<template>
    <div class="partition">
        <div v-wechat-title='$route.meta.title'></div>
        <div class="header">{{$route.meta.title}}</div>
        <ul class="partition_ul">
            <li class="partition_1" v-for="(item,index) in lart" @click="indexdmdm(index)">{{item.name}}</li>
            <!-- <li class="partition_1" @click="indexdmdm(index)">敏感肌肤专区</li>
            <li class="partition_2">暗黄色斑专区</li>
            <li class="partition_3">痘痘痤疮专区</li>
            <li class="partition_4">抗皱抗衰专区</li>
            <li class="partition_5">日常补水保湿专区</li>
            <li class="partition_5">全部分类</li> -->
        </ul>

    </div>
</template>
<script>
    // import data from '../../../static/indexovr1.json'
    export default {
        name: 'partition',
        data() {
            return {
                lart: [
                    {name:"敏感肌肤专区"},
                    {name:"暗黄色斑专区"},
                    {name:"痘痘痤疮专区"},
                    {name:"抗皱抗衰专区"},
                    {name:"日常补水保湿专区"},
                    {name:"全部分类"},

                ],
                dataumkk: [],
                ovtrn: []


            }
        },
        mounted() {
            // this.ovtrn = data.ovtrn;
            // console.log(this.ovtrn)

        },
        methods: {
            indexdmdm(index) {
                console.log(index)
                this.$router.push({ path: "/index", query: { id: index } })
                // console.log(index)
                // console.log(this.ovtrn[index])

            }
        },

        // beforeCreate: function () {
        //     this.$axios.get("./static/indexovr.json", {}).then((res) => {
        //         console.log(res)
        //         this.dataumkk = res.data.ovtrn
        //         console.log(this.dataumkk)
        //     })
        // },

    }
</script>
<style>
    .partition {
        width: 100%;
        min-height: 120px;
        /* background: #eeeeee; */
        padding: 10px;
        box-sizing: border-box;
    }

    .header {
        width: 100%;
        height: 32px;
        background: #fb6d27;
        text-align: center;
        line-height: 31px;
        font-size: 20px;
        font-weight: 600;
        border-radius: 5px;
        color: #ffffff;
    }

    .partition_ul {
        width: 100%;

    }

    .partition_ul li {
        width: 48%;
        height: 126px;
        float: left;
        margin-left: 4%;
        margin-top: 14px;
        border-radius: 10px;
        text-align: center;
        line-height: 126px;
        font-size: 20px;
        font-weight: 600;
        color: #ffffff;
        background: #fb6d27;
    }

    .partition_ul li:nth-of-type(2n+1) {
        margin-left: 0;
    }
</style>


