                    <p class="index_introduce">{{item.set_meal_effect[1].set_meal_text}}</p> <!-- 详情 -->
                    
                    <p class="index_price">￥{{item.set_meal_price}}&nbsp;&nbsp;<span
                            class="original">原价<s>￥{{item.set_meal_originalprice}}</s></span></p><!-- 活动价/原价 -->
                </li>
            </ul>
            <!-- 抗皱抗衰专区 -->
            <div class="wvaq" v-if="pref_show3">抗皱抗衰专区</div>
            <ul class="index_ul" v-for="(item,index) in ovtrn3" @click="particulars(item.id,item.prefecture)">
                <li class="index_img">
                    <img :src="item.img_url[0].url" alt="">
                </li>
                <li class="index_text">
                    <p class="index_name">{{item.set_meal_name}}</p><!-- 套餐名称 -->
                    <p class="index_introduce">{{item.set_meal_effect[1].set_meal_text}}</p> <!-- 详情 -->
                    
