</template>
<script>
    import data0 from '../../../static/indexovr1.json'
    import data1 from '../../../static/indexovr2.json'
    import data2 from '../../../static/indexovr3.json'
    import data3 from '../../../static/indexovr4.json'
    import data4 from '../../../static/indexovr5.json'
    export default {
        name: 'index',
        data() {
            return {
                dataumkk: [],
                ovtrn0: [],
                ovtrn1: [],
                ovtrn2: [],
                ovtrn3: [],
                ovtrn4: [],
                // ovt:1,
                partitionid:[],
                pref_showtext:"",
                pref_show:true,
                pref_show0:false,
                pref_show1:false,
                pref_show2:false,
                pref_show3:false,
                pref_show4:false,


            }
        },
        mounted() {
            this.partitionid = this.$route.query.id;//来自哪一个专区
            console.log(this.partitionid);
            if(this.partitionid==0){
                // 敏感肌肤专区
                this.ovtrn0 = data0.ovtrn;
                this.pref_showtext="敏感肌肤专区";
            }else if(this.partitionid==1){
                //暗黄色斑专区
                this.ovtrn0 = data1.ovtrn;
                this.pref_showtext="暗黄色斑专区";
            }else if(this.partitionid==2){
                //痘痘痤疮专区
                this.ovtrn0 = data2.ovtrn;
                this.pref_showtext="痘痘痤疮专区";
            }else if(this.partitionid==3){
                //抗皱抗衰专区
                this.ovtrn0 = data3.ovtrn;
                this.pref_showtext="抗皱抗衰专区";
            }else if(this.partitionid==4){
                //日常补水保湿专区
                this.ovtrn0 = data4.ovtrn;
                this.pref_showtext="日常补水保湿专区";
            }else if(this.partitionid==5){
                //全部分类
                this.ovtrn0 = data0.ovtrn;
                this.ovtrn1 = data1.ovtrn;
                this.ovtrn2 = data2.ovtrn;
                this.ovtrn3 = data3.ovtrn;
                this.ovtrn4 = data4.ovtrn;
                this.pref_show=false;
                this.pref_show0=true;
                this.pref_show1=true;
                this.pref_show2=true;
                this.pref_show3=true;
                this.pref_show4=true;
                
                  
            }

        },
        methods: {
            particulars(id,prefecture) {
                this.$router.push({ path: "/particulars", query: { id: id ,prefecture:prefecture} })
                // console.log(index)
                // console.log(this.ovtrn[index])

            }
        },

        // beforeCreate: function () {
        //     this.$axios.get("./static/indexovr.json", {}).then((res) => {
        //         console.log(res)
        //         this.dataumkk = res.data.ovtrn
        //         console.log(this.dataumkk)
        //     })
        // },

    }
</script>
<style>
    .index_box {
        width: 100%;
        min-height: 120px;
        /* background: #eeeeee; */
        padding: 10px;
        box-sizing: border-box;
    }

    .header {
        width: 100%;
        height: 25px;
        background: #fb6d27;
        text-align: center;
        line-height: 25px;
        font-size: 20px;
        font-weight: 600;
        color: #ffffff;
    }

    .index_ul {
        width: 100%;
        height: 120px;
        background: #ffffff;
        margin-bottom: 10px;
        border: 1px solid #cacaca;
    }

    .index_ul li {
        float: left;
    }

    .index_img {
        width: 120px;
        height: 120px;
        /* background: green; */
        border-radius: 10px;
    }

    .index_img img {
        width: 100%;
        height: 100%;
    }

    .index_text {
        width: 225px;
        height: 120px;
        /* background: yellow; */
        padding: 10px 5px;
        box-sizing: border-box;
        margin-left: 10px;
        position: relative;
        /* border-radius: 10px; */

    }

    .index_name {
        width: 100%;
        height: 20px;
        line-height: 20px;
        /* background: red; */
        font-weight: 600;
        color: red;
        /* 超出文字 */
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
    }

    .index_introduce {
        width: 100%;
        max-height: 60px;
        /* background: green; */
        line-height: 20px;
        letter-spacing: 1.5px;
        font-size: 10px;
        font-weight: 900;
        /* 超出文字 */
        word-break: break-all;
        text-overflow: ellipsis;
        display: -webkit-box;
        -webkit-box-orient: vertical;
        -webkit-line-clamp: 3;
        overflow: hidden;

    }

    .index_price {
        position: absolute;
        width: 214px;
        bottom: 0;
        height: 20px;
        color: red;
        font-weight: 900;
        font-size: 16px;
        /* background: blue; */
        box-sizing: border-box;
        line-height: 20px;
        /* 超出文字 */
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;

    }

    .original {
        font-size: 10px;
    }
    .wvaq{
        width: 50%;
        height: 30px;
        text-align: center;
        line-height: 30px;
        background: #fb6d27;
        margin: 0 auto 5px;
        color: #ffffff;
    }
</style>


