<template>
    <div class="particulars">
        <div v-wechat-title='$route.meta.title'></div>
        <div class="parti">
            <ul class="parti_ul">
                <li class="parti_img">
                    <van-swipe class="my-swipe" :autoplay="3000" indicator-color="white">
                        <van-swipe-item v-for="(item,index) in imgurl">
                            <img :src="item.url" alt="">
                        </van-swipe-item>
                    </van-swipe>

                </li>
                <li class="parti_price">￥{{ovtrn.set_meal_price}} &nbsp;&nbsp;<span
                        class="parti_dr">原价<s>￥{{ovtrn.set_meal_originalprice}}</s></span></li>
                <li class="parti_introduce" v-for="(item,index) in text">{{item.set_meal_text}}</li>
            </ul>

        </div>

















        <!--  -->
        <!-- <div class="parti">
            <ul class="parti_ul">
                <li class="parti_img">
                    <img :src="ovtrn.img_url" alt="">
                </li>
                <li class="parti_price">￥{{ovtrn.set_meal_price}} &nbsp;&nbsp;<span
                        class="parti_dr">原价<s>￥{{ovtrn.set_meal_originalprice}}</s></span></li>
                <li class="parti_introduce">{{ovtrn.set_meal_effect}}</li>
            </ul>

        </div> -->

    </div>
</template>
<script>
    import data0 from '../../../static/indexovr1.json'
    import data1 from '../../../static/indexovr2.json'
    import data2 from '../../../static/indexovr3.json'
    import data3 from '../../../static/indexovr4.json'
    import data4 from '../../../static/indexovr5.json'
    export default {
        name: "particulars",
        data() {
            return {
                id: [],//哪一个id
                prefecture: [],//哪一个专区
                ovtrn: [],
                imgurl: [],
                text: []
            }
        },
        mounted() {
            this.prefecture = this.$route.query.prefecture;//判断哪一个专区
            this.id = this.$route.query.id;//判断id

            console.log(this.id)
            console.log(this.prefecture)
            if (this.prefecture == 0) {
                this.ovtrn = data0.ovtrn[this.id];
                this.imgurl = data0.ovtrn[this.id].img_url;
                this.text = data0.ovtrn[this.id].set_meal_effect;
            }else if(this.prefecture == 1) {
                this.ovtrn = data1.ovtrn[this.id];
                this.imgurl = data1.ovtrn[this.id].img_url;
                this.text = data1.ovtrn[this.id].set_meal_effect;
            }else if(this.prefecture == 2) {
                this.ovtrn = data2.ovtrn[this.id];
                this.imgurl = data2.ovtrn[this.id].img_url;
                this.text = data2.ovtrn[this.id].set_meal_effect;
            }else if(this.prefecture == 3) {
                this.ovtrn = data3.ovtrn[this.id];
                this.imgurl = data3.ovtrn[this.id].img_url;
                this.text = data3.ovtrn[this.id].set_meal_effect;
            }else if(this.prefecture == 4) {
                this.ovtrn = data4.ovtrn[this.id];
                this.imgurl = data4.ovtrn[this.id].img_url;
                this.text = data4.ovtrn[this.id].set_meal_effect;
            }

        },
        created: function () {
            //     this.$axios.get("./static/indexovr.json", {}).then((res) => {
            //         console.log(res)
            //         this.dataumkk = res.data.ovtrn
            //         console.log(this.dataumkk)
            //     })
        },
    }
</script>
<style>
    .parti_ul {
        width: 100%;

    }

    .parti_img {
        width: 100%;

    }

    .parti_img img {
        width: 100%;
    }

    .parti_price {
        width: 100%;
        height: 30px;
        /* background: green; */
        line-height: 30px;
        font-size: 20px;
        font-weight: 600;
        color: red;
    }

    .parti_dr {
        font-size: 15px;
        color: #969696;
    }

    .parti_introduce {
        width: 100%;
        font-size: 22px;
        line-height: 32px;
        padding: 10px;
        box-sizing: border-box;
    }

    /* 轮播图 */
    .my-swipe .van-swipe-item {
        color: #fff;
        font-size: 20px;
        /* line-height: 150px; */
        text-align: center;
        /* background-color: #39a9ed; */
    }

    .van-swipe-item {
        width: 375px;
        height: 375px;
    }

    .van-swipe-item img {
        width: 100%;
        height: 100%;
    }
</style>


