import Vue from 'vue'
import Router from 'vue-router'


import Index from '../components/index/index'

import Particulars from '../components/index/particulars'

import Partition from '../components/index/partition'//分区






Vue.use(Router)

export default new Router({
	routes: [
	
		// {
		// 	path: '/',
		// 	name: 'index',
		// 	component: Index,
		// 	// hidden: true,
		// 	meta: {
		// 		title: '618活动',
		// 		requireAuth: true
		// 	}
		// },
		{
			path: '/index',
			name: 'index',
			component: Index,
			// hidden: true,
			meta: {
				title: '618活动',
				requireAuth: true
			}
		},
		{
			path: '/particulars',
			name: 'particulars',
			component: Particulars,
			// hidden: true,
			meta: {
				title: '商品详情',
				requireAuth: true
			}
		},
		{
			path: '/',
			name: 'partition',
			component: Partition,
			// hidden: true,
			meta: {
				title: '产品分区',
				requireAuth: true
			}
		},


		
	]
})



